/**
 * @description 
 * @param {Date} dateTime 
 * @param {String} formatStr  yyyy:mm:dd|yyyy:mm|yyyymmdd|yyyymmdd hhMM, yyyy-mm-dd
 * @returns {string} 
 */
export function timeFormat(dateTime = null, formatStr = 'yyyy-mm-dd') {
    let date
    // 
    if (!dateTime) {
        date = new Date()
    }
    // unix
    else if (/^\d{10}$/.test(dateTime.toString().trim())) {
        date = new Date(dateTime * 1000)
    }
    // new Date
    else if (typeof dateTime === 'string' && /^\d+$/.test(dateTime.trim())) {
        date = new Date(Number(dateTime))
    }
    //  RFC 2822 
    else {
        // Safari/Webkitnew Date/
        date = new Date(
            typeof dateTime === 'string' ?
            dateTime.replace(/-/g, '/') :
            dateTime
        )
    }

    const timeSource = {
        'y': date.getFullYear().toString(), // 
        'm': (date.getMonth() + 1).toString().padStart(2, '0'), // 
        'd': date.getDate().toString().padStart(2, '0'), // 
        'h': date.getHours().toString().padStart(2, '0'), // 
        'M': date.getMinutes().toString().padStart(2, '0'), // 
        's': date.getSeconds().toString().padStart(2, '0') // 
        // 
    }

    for (const key in timeSource) {
        const [ret] = new RegExp(`${key}+`).exec(formatStr) || []
        if (ret) {
            // 
            const beginIndex = key === 'y' && ret.length === 2 ? 2 : 0
            formatStr = formatStr.replace(ret, timeSource[key].slice(beginIndex))
        }
    }

    return formatStr
}
// 
export function computeTime(smallTime, bigTime, formatStr = "s:0.00") {
    const cha = bigTime - smallTime
    // [unit, s/ms ] [bits, ]
    const [unit, bits] = formatStr.split(":")
    let overTime;
    if (unit == 's') {
        const time = cha / 1000
        if (bits == "0.00") {
            overTime = formatTime0_00(time)
        }
    }
    return overTime
}

// 0.00
function formatTime0_00(str) {
    if (str === 0) {
        return "0"
    }
    let arr = str.toString().split('.');
    let integer = arr[0]?.padStart(1, '0'); // 
    let decimal = arr[1]?.padEnd(2, '0') || "00"; // 

    return integer + '.' + decimal;
}

/**
 * 
 * @param {Array} data - 
 * @param {String} key - , 
 * @returns {Object} - 
 */
export function groupByDate(data, key) {
    const result = {};
    data.forEach(item => {
        // ex: 2023-06-17
        const date = timeFormat(new Date(item[key]).toLocaleDateString())
        if (!result[date]) {
            // 
        result[date] = {
            data:[],
            sortType: 'down'
        };
        }
        // 
        result[date].data.push(item);
    });

    return result;
}

/**
 * 
 * @param {Array} result - 
 * @param {String} key - , 
 * @returns {Object} - 
 */
export function sortByTimeAndGroupByDate(result, key, type="up") {
    // [key] 
    if(type == "up"){
        result.sort((a, b) => a[key] - b[key]);
    }else if(type == "down"){
        result.sort((a, b) => b[key] - a[key]);
    }
    console.log(result);

    return result;
}

